.cookies_propmpt {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  padding: 10px;
}
.cookies_propmpt button {
  padding: 7px;
  border-radius: 15px;
  border: none;
  white-space: nowrap;
}
.cookies_propmpt button:focus,
.cookies_propmpt button:active {
  outline: none;
}
